// Chat Talk 모듈 스타일
:host {
  display: block;
  height: 100%;
  width: 100%;
}

.chat-talk-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  background: #f5f7fa;
}

.chat-talk-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 24px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 10;

  .header-left {
    display: flex;
    align-items: center;
    gap: 12px;

    .logo-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
    }

    h1 {
      font-size: 20px;
      font-weight: 600;
      margin: 0;
    }
  }

  .header-right {
    display: flex;
    align-items: center;
    gap: 16px;

    .user-info {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px 16px;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 20px;
      font-size: 14px;

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }
    }

    button {
      color: white;

      &:hover {
        background: rgba(255, 255, 255, 0.1);
      }
    }
  }
}

.chat-talk-content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

// 모바일 반응형
@media (max-width: 768px) {
  .chat-talk-header {
    padding: 12px 16px;

    .header-left {
      h1 {
        font-size: 16px;
      }

      .logo-icon {
        font-size: 24px;
        width: 24px;
        height: 24px;
      }
    }

    .header-right {
      gap: 8px;

      .user-info {
        padding: 6px 12px;
        font-size: 12px;

        span {
          display: none;
        }
      }
    }
  }
}
